import React, { Component, PropTypes } from 'react';
import { Router, Route, Link ,hashHistory} from 'react-router';
/* input  */
import Input from '../../module/input/input';
/*  */
import { SegmentedControl,Picker, List,  Toast, WhiteSpace , WingBlank, InputItem} from 'antd-mobile';
/*  */
/*  */
import Title from '../../module/navTitle/navTitle';


// start
import './coupon.scss'
// end
var _that=null;
var codeTime=null;
class Coupon extends Component{
    constructor(){
        super();
    }
    componentWillMount(){
        _that=this;
        //
        this.state={

        }
    }
    componentDidMount(){
        //  start
        componentStore.set(this);
        //  end
    };
    componentWillUnmount(){
        //  start
        componentStore.clear(this);
        //  end
    };
    render(){
        //console.log("nameValue"+this.state.nameValue)

        return (
            <div>
                <Title $id="coupon"
                       title=""
                       retreat={this.retreat}
                       grade="2"
                       munIng=""
                >
                </Title>
                <div className="coupon">
                    <div className="tab">
                        <SegmentedControl values={['', '','']} tintColor={"#ff7405"}   />
                    </div>
                    <div className="content">
                        <div className="coupon_quan">
                            <div className="img_bg">
                                <div className="money">
                                    <div className="money_img"></div>
                                </div>
                                <div className="cut">
                                    10000 
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

}
export default Coupon;



